.colors-wrapper li {
	height: 90px;
	width: 90px;
	border: none;
	position: absolute;
	border-radius: 50%;
	-webkit-transform: scale(0.8);
	    -ms-transform: scale(0.8);
	        transform: scale(0.8);
	-webkit-transition: z-index .6s ease-in-out, -webkit-transform .6s ease-in-out;
	transition: z-index .6s ease-in-out, -webkit-transform .6s ease-in-out;
	-o-transition: z-index .6s ease-in-out, transform .6s ease-in-out;
	transition: z-index .6s ease-in-out, transform .6s ease-in-out;
	transition: z-index .6s ease-in-out, transform .6s ease-in-out, -webkit-transform .6s ease-in-out;
	display: -ms-flexbox;
	display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.colors-wrapper li:hover {
	z-index: 999;
	-webkit-transform: scale(0.9);
	    -ms-transform: scale(0.9);
	        transform: scale(0.9);
	cursor: pointer;
}

/*** Start of phone styles ***/
@media screen and (max-width: 479px) {

	.colors-wrapper li {
		height: 75px;
		width: 75px;
		font-size: 14px;
	}
}


